.single-image {
    &-modal {
        position: relative;
        overflow: hidden;
    }
    
    .comments {
        padding: 1em 0.5em;
        background-color: $secondaryColor;
        &-user {
            height: 100px;
            margin: 1em 0;
            
            textarea {
                width: 100%;
                height: 100%;
                resize: none;
                border-radius: 5px;
                padding: 0.5em;
                &:focus {
                    outline: none !important;
                }
            }
        }

        &-all {
            padding: 1em 0;
            ul {
                list-style: none;
                border-radius: 10px;
                overflow: hidden;
                li {
                    padding: 0.5em;
                    .comment-top {
                        &-name {
                            margin-right: 1em;
                            font-weight: bold;
                        }
                    }
                    .comment-bottom {
                        margin: 0.25em 0;
                        font-size: 0.9em;
                    }
                }
                .even {
                    background: #fffff3;
                }
            }
        }
    }
}
    
.dog-cart-image-left, .dog-cart-image-right {
    position: absolute;
    top: 0;
    height: 100%;
    width: 20%;
    scale: 2;
    color: $secondaryColor;
    opacity: 0;
    transition: 0.5s;
    cursor: pointer;
    @include flex(center);
    &:hover {
        opacity: 1;
    }
}

.modal .dog-cart-image-left, .modal .dog-cart-image-right {
    scale: 1;
}

.dog-cart-image-left {
    left: 0;
    background: linear-gradient(to left, #33333300, #333 100%);
}
.dog-cart-image-right {
    right: 0;
    background: linear-gradient(to right, #33333300, #333 100%);
}