.single-breed {
        &-image__container {
            width: 50%;
            padding: 1em;
            .single-breed-image {
                height: 450px;
                box-shadow: 1px 1px 1em;
                border-radius: 10px;
                overflow: hidden;
            }   
        }
        &-info__container {
            padding-left: 2em;
            width: 50%;
            div {
                padding: 0.75em 0;
                span {
                    font-weight: bold;
                }
            }

        }
}

@media screen and (max-width: 1000px) {
    .single-breed {
        &__container {
            flex-direction: column !important;
        }
        &-image__container {
            width: 100% !important;
        }

        &-info__container {
            width: 100% !important;
        }
    }
}

@media screen and (max-width: 500px) {
    .single-breed {
        &-image__container {
            .single-breed-image {
                height: 350px;
            }
        }
        &-info__container {
            padding-left: 1em;
        }
    }
}