.breeds {
    &-list {
        h2 {
            color: $primaryColor;
            text-align: center;
            font-size: 2em;
            text-shadow: 1px 1px 1px black;
            text-transform: uppercase;
        }        
        &-search {
            text-align: center;
            margin-top: 1em;
            input {
                width: 50%;
                padding: 0.5em;
                border: none;
                border-radius: 10px;
                &:focus {
                    outline: none !important;
                }
            }
        }
        ul {
            list-style: none;
            @include flex(center);
            li {
                cursor: pointer;
                &:hover {
                    color: $primaryColor;
                }
            }
        }
        &__container {
        align-items: stretch;
        flex-wrap: wrap;
        padding: 2em;
        a {
            padding: 1em;
            width: 30%;
            li{
                transition: 0.5s;
                .breeds-name {
                    font-size: 1.5em;
                }
            }
        }
    }
    .pagination {
        &-list__container {
            
            li {
                padding: 0.25em;
            }
            .active {
                color: $primaryColor;
            }
        }
    }
}
}

@media screen and (max-width: 1000px) {
    .breeds-list__container {
        .breeds-name {
            font-size: 1.2em !important;
        }
        .breeds-description {
            font-size: 0.8em !important;
        }
    }
}

@media screen and (max-width: 600px) {
    .breeds-list__container {
        padding: 0 !important;
        justify-content: space-between !important;
            .breeds-name {
                font-size: 1em !important;
            }
            .breeds-description {
                font-size: 0.7em !important;
            }
    }
}
