.checkbox-wrapper {
    margin: 0.5em !important;

    .checkbox {
        appearance: none;
        background-color: #dfe1e4;
        border-radius: 72px;
        border-style: none;
        flex-shrink: 0;
        height: 20px;
        margin: 0;
        position: relative;
        width: 30px;
        transition: all 100ms ease-out;
        &::before {
            bottom: -6px;
            content: "";
            left: -6px;
            position: absolute;
            right: -6px;
            top: -6px;
          }

          &::after {
            transition: all 100ms ease-out;
            background-color: #fff;
            border-radius: 50%;
            content: "";
            height: 14px;
            left: 3px;
            position: absolute;
            top: 3px;
            width: 14px;
          }
          &:hover {
            background-color: #c9cbcd;
            transition-duration: 0s;
          }

          &:checked {
            background-color: $primaryColor;
            &::after {
                background-color: #fff;
                left: 13px;
            }

            &:hover {
                background-color: $primaryColor;
              }
          }
      }

      :focus:not(.focus-visible) {
        outline: 0;
      }

      input[type=checkbox] {
        cursor: default;
      }
}
