.modal {
    z-index: 100;
    @include absolute(5%, 50%);
    transform: translate(-50%, 0);
    background: white;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
    border-radius: 8px;
    overflow: hidden;
  
  .modal__header {
    width: 100%;
    padding: 1rem 0.5rem;
    background: $primaryColor;
    box-shadow: 1px 1px 1px black;
    color: white;
    h2 {
        text-align: center;
        margin: 0.5rem;
        font-size: 2em;
    }
  }
  
  .modal__footer {
    padding: 1rem 0.5rem;
  }

}
  
//   @media (min-width: 800px) {
//     .modal {
//       left: calc(50% - 20rem);
//       width: 40rem;
//     }
//   }
  
  .modal-enter {
    top: 0;
    opacity: 0;
  }
  
  .modal-enter-active {
    top: 5%;
    opacity: 1;
    transition: all 200ms;
  }
  
  .modal-exit {
    top: 5%;
    opacity: 1;
  }
  
  .modal-exit-active {
    top: 0;
    opacity: 0;
    transition: all 200ms;
  }

  @media screen and (max-width: 800px) {
    .modal {
        width: 90vw;
    }
}