.navigation {
    @include flex(space-between);
    width: 100vw;
    height: 107px;
    background-color: $primaryColor;
    padding: 1em 3em;
    position: fixed;
    border-bottom: 1px solid black;
    box-shadow: 1px 1px 1em;
    z-index: 2;
    &-logo {
        cursor: pointer
    }

    &-title {
        cursor: pointer;
        h1{
            color: $secondaryColor;
            text-shadow: 1px 1px 1px black;
            font-size: 2em;
        }
    }

    &-menu {
        .fa-bars {
            scale: 2;
            display: block;
            margin: auto;
        }
        
        ul {
            @include flex(space-between);
            li {
                padding: 0.25em 1em;
                cursor: pointer;
                transition: 0.5s;
                border-bottom: 2px solid $primaryColor;
                font-size: 1.2em;
                color: $textColor;
                &:hover {
                    border-bottom: 2px solid $thirdColor;
                }
            }
            .avatar-container {
                &:hover {
                    border-bottom: 2px solid $primaryColor;
                }
                .avatar {
                    margin: auto;
                    border-radius: 50%;
                    overflow: hidden;
                    
                }
            }
        }

        &-bar {
            transition: 1s;
            flex-direction: column;
            @include absolute(100%, 0%);
            align-items: stretch !important;
            transform: translate(0, 0);
            width: 100vw;
            height: 0;
            border-bottom: 0px solid $primaryColor;
            overflow: hidden;

            li {
                // border: none !important;
                text-align: center !important;
                padding: 0.5em !important;
                background-color: $secondaryColor !important;
            }
        }

        .hamburger-active {
            height: 220px !important;
            border-bottom: 2px solid $primaryColor;
        }
    }
}