.image {
    height: 100px;
    width: 100px;
}

.image-small {
    height: 50px !important;
    width: 50px !important;
}

.image-medium {
    height: 75px !important;
    width: 75px !important;
}

.image-medium-large {
    height: 150px !important;
    width: 150px !important;
}

.image-large {
    height: 500px !important;
    width: 500px !important;
}

.center {
    margin: auto;
}

.image-upload__preview {
    padding-bottom: 1em;
    .images-upload__container {
        @include flex(center);
        flex-wrap: wrap;
        li {
            position: relative;
            span {
                @include absolute(7%, 90%);
                color: red;
                cursor: pointer;
                font-weight: bold;
                transition: 0.25s;
                font-size: 1.5em;
                text-shadow: 1px 1px 1em black;
                opacity: 0;
            }
            .active {
                opacity: 1;
            }
        }
    }
}

@media screen and (max-width: 500px) {
    .image {
        height: 75px;
        width: 75px;
    }
    
    .image-small {
        height: 25px !important;
        width: 25px !important;
    }
    
    .image-medium {
        height: 50px !important;
        width: 50px !important;
    }
    
    .image-medium-large {
        height: 100px !important;
        width: 100px !important;
    }
    
    .image-large {
        height: 250px !important;
        width: 250px !important;
    }
}