.home {
    &-header {
        padding: 2em;
        @include flex(center);
        z-index: 1;
        &-image {
            width: 20%;
        }
        &-info {
            padding: 2em;
            &-title {
                h1 {
                    font-size: 4em;
                    color: $primaryColor;
                    text-shadow: 1px 1px 2px black;
                }
            }
            &-description {
                font-size: 1.5em;
                text-shadow: 1px 1px 1px black;
                padding: 0.5em;
                p {
                color: $secondaryColor;
                padding: 0.5em 0;
                span {
                        padding-left: 0.25em;
                }
                .smile {
                        color: gold;
                }
                .heart {
                    color: red;
                }
                .arrow {
                    color: black;
                }
            }
            }
        }
    }

    &-services {
        z-index: 1;
        @include flex(center);
        padding: 3em;
        margin: 1em;
        .home-services_container {
            margin: 1em;
            text-align: center;
            background: $secondaryColor;
            width: 400px;
            height: 300px;
            border-radius: 20px;
            padding: 1em;
            box-shadow: 5px 5px 1em $primaryColor;
            @include flex(space-between);
            flex-direction: column;
            align-items: normal;
            svg {
                width: 100px;
                height: 100px;
                color: $primaryColor;
                display: block;
                margin: auto;
            }
            p {
                font-size: 1.5em;
                padding: 1em;
            }
        }
    }
}

@media screen and (max-width: 1300px) {
    .home {
        &-header {
            padding-bottom: 0 !important;
        }
        &-services_container {
            p {
                font-size: 1.1em !important;
            }
        }
    }
}

@media screen and (max-width: 1050px) {
    .home {

        h1 {
            font-size: 3em !important;
        }
        
        &-header-image {
            display: none;
        }

        // .header-image-left {
        //     top: 20%;
        //     right: 10%;
        // }

        // .header-image-right {
        //     top: 30%;
        //     right: 5%;
        // }

        &-services {
            padding: 0 !important;
            flex-direction: column;
            
            p {
                font-size: 1.5em !important;
            }
        }
    }
}

@media screen and (max-width: 600px) {
    .home {
        &-header-info {
            padding: 1em;
            text-align: center;
            h1 {
                font-size: 2.2em !important;
                margin-bottom: 1em !important;
            }
            
            p {
                padding: 0.5em 0;
                span {
                    display: block;
                    padding: 0.25em;
                }
            }
        }
    }
}

@media screen and (max-width: 500px) {
    .home {
        &_container {
            width: 100% !important;
        }
    }
}