@import "./variables";
@import "./mixins";
@import "./checkbox";

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: "Roboto", sans-serif;
    border-color: $borderColor;
}

body {
    background-color: $thirdColor;
}

img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

h1, h2, h3, h4, h5, p, span {
    color: $textColor;
}

h1 {
    font-size: 2em;
}

input, textarea {
    &:focus {
        outline: none !important;
    }
}

ul {
    list-style: none;
}

a {
    text-decoration: none;
    color: $textColor;
}

.App {
    background-image: url('../images/background-992858_1280.png');
    background-attachment: fixed;
    min-height: 100vh;
}

.body {
    
    & > div:first-child {
        padding-top: 6em;
        padding-bottom: 1em;
        min-height: 100vh;
    }
}

.flex-container {
    @include flex(space-between);
}

.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0.75);
    z-index: 10;
}

@import "./button";
@import "./modal";
@import "./card";
@import "./image";
@import "./home";
@import "./navigation";
@import "./footer";
@import "./list";
@import "./single-item";
@import "./cart";
@import "./single-image";
@import "./breeds";
@import "./single-breed";
@import "./modal-form";
@import "./admin";