.cart-container {
    margin-top: 1em;
    align-items: stretch;
    .cart-items-list {
        text-align: center;
        width: 60%;
        padding: 1em 2em;
        position: relative;

        &-empty {
            @include absolute(50%, 50%);
            width: 100%;
        }
        ul {
            @include flex(center);
            flex-direction: column;
            align-items: start;
            li {
                @include flex(space-between);
                width: 100%;
                padding: 1em 0;
                font-size: 1.5em;
                div {
                    width: 20%;
                    text-align: center;
                }
                .cart-item-image {
                    border-radius: 5px;
                    overflow: hidden;
                }

                .cart-item-name {
                    width: 40%;
                }
                .cart-item-quantity {
                    
                    .minus, .plus {
                        cursor: pointer;
                        display: inline-block;
                        width: 25px;
                    }
                }
            }
        }
    }
    .cart-info {
        width: 40%;
        padding: 1em;
        &-title {
            text-align: center;
        }
        &-description {
            margin-top: 1em;
            text-align: justify;
        }
        &-amount {
            margin-top: 3em;
            color: $textColor;
            span {
                font-weight: bold;
            }
        }
        &-button {
            margin-top: 1em;
        }
    }
}

@media screen and (max-width: 1200px) {
    .cart-container {
        flex-direction: column-reverse;
        .cart-items-list {
            width: 100%;
        }
        .cart-info {
            width: 65%;
            margin: auto;
        }
    }
}

@media screen and (max-width: 650px) {
    .cart-container {
        .cart-info {
            width: 100%;
        }
        .cart-items-list {
            padding: 0 !important;
            li {
                font-size: 1em !important;
            }
        }
    }
}

@media screen and (max-width: 450px) {
    .cart-container {
        .cart-items-list {
            
            
        }
    }
}