.modal-form {
    width: 700px;
    text-align: center;
    padding: 2em;
    padding-top: 0.5em;
    background-color: $secondaryColor;
    div[class^='modal-form-'], div[class*=' modal-form-']{
        padding: 0.75em;
    }

    label {
        display: block;
        margin-bottom: 0.25em;
    }
    input, textarea {
        width: 300px;
        border-radius: 10px;
        padding: 0.25em 0.5em;
        font-size: 1.1em;
        resize: none;
        text-align: center;
    }

    p {
        margin-top: 0.25em;
        span {
            color: #156ece;
            cursor: pointer;
            transition: 0.5s;
            &:hover {
                color: #0b3767;
            }
        }
    }
}

@media screen and (max-width: 800px) {
    .modal-form {
        width: 100%;
    }
}