.card {
    background-color: $secondaryColor;
    border: 2px solid $borderColor;
    border-radius: 10px;
    padding: 1em 2em;
    margin: 5em 8em;
    min-height: 500px;
}

@media screen and (max-width: 900px) {
    .card {
        margin: 5em 1em;
    }
}

@media screen and (max-width: 500px) {
    .card {
        padding: 1em;
    }
}