.list {
    justify-content: left;
    align-items: stretch;
    padding-bottom: 0 !important;

    &-left {
        flex-basis: 20%;
        background-color: $thirdColor;
        text-align: center;
        padding: 1em 2em;
        position: relative;
        &-close {
            @include absolute(5%, 80%);
        }
        h2 {
            color: $textColor;
            text-shadow: 1px 1px 1px $primaryColor;
            margin-top: 1em;
            font-size: 2em;
            text-transform: uppercase;
        }
        div {
            margin: 2em 0;
            
            label {
                color: $textColor;
                font-weight: bold;
                margin-bottom: 0.25em;
                display: block;
            }

            input, select {
                width: 100%;
            }

            input[type=text] {
                padding: 0.5em 1em;
                border-radius: 5px;
                border: none;
                background: $secondaryColor;
            }
            input[type=range] {
                -webkit-appearance: none;
                appearance: none;
                background: transparent;
                cursor: pointer;
            }

            input[type=range]::-webkit-slider-runnable-track {
                background: $secondaryColor;
                height: 0.5rem;
                border-radius: 10px;
              }
              
              /******** Firefox ********/
              input[type=range]::-moz-range-track {
                background: #053a5f;
                height: 0.5rem;
                border-radius: 10px;
              }

              input[type=range]::-webkit-slider-thumb {
                -webkit-appearance: none; /* Override default look */
                appearance: none;
                margin-top: -0.5rem; /* Centers thumb on the track */
                background-color: black;
                height: 1.5rem;
                width: 0.5rem;
             }
             #input-price-value {
                margin: 0.5em auto;
                font-weight: bold;
                width: 40px;
                display: block;
                font-size: 0.8em;
             }
             #breed-select, #category-select {
                padding: 0.5em 1em;
                border-radius: 5px;
                border: none;
                background: $secondaryColor;
                color: #8b7f79;

                option {
                    color: black;
                }

                option:nth-child(1) {
                    color: #8b7f79;
                }
             }

             .select-valid {
                color: black !important;
             }
        }
    }

    &-right {
        margin: 3em 2em;
        position: relative;
        flex: 1;
        ul {
            @include flex(center);
            flex-wrap: wrap;
        }

        &-button {
            display: flex;
            justify-content: space-around;
            width: 30%;
            margin: 2em auto;
        }

        &__container {
            flex-basis: 45%;
            margin: 1em;
            background: $secondaryColor;
            border: 1px solid $borderColor;
            border-radius: 10px;
            padding: 1em;

            .cart-info {
                @include flex(space-around);
                font-size: 1.5em;
                margin-top: 0.5em;
                border-top: 1px dotted $borderColor;
                
            }
        }
    }
}

.dog-cart {
    flex-basis: 45%;
    &-image {
        border-radius: 10px;
        overflow: hidden;
        position: relative;
        cursor: pointer;
        img {
            object-fit: contain;
        }
    }
    &-info {
        padding-top: 0.5em;
        &-left {
            @include flex(center);
            flex-direction: column;
            align-items: start;
            .dog-cart-breed {
                font-size: 0.6em;
            }
        }
        &-right {
            @include flex(center);
            
            .dog-cart-like, .dog-cart-comments {
                @include flex(center);
                flex-direction: column;
                margin: 0 0.25em;
                span {
                    font-size: 0.75em;
                }
            }
        
        .dog-cart-like {
            cursor: pointer;
            color: #ff0057;
            svg {
                transition: 0.5s;
                &:hover {
                    scale: 1.1;
                }
            }
        }
    }
    }
}

.product-cart {
    flex-basis: 30%;
    &-image {
        height: 200px;
        width: 200px;
        margin: auto;
        padding: 1em;
    }
    &-info {
        padding: 0.75em 0;
        font-size: 1.2em !important;
        
    &-price {
        font-weight: bold;
        background: white;
        border-radius: 50%;
        width: 50px;
        height: 50px;
        position: relative;
        font-size: 0.75em !important;
        text-align: center;

        div {
            @include absolute(50%, 50%);
            width: 100%;
        }
    }
}
}

@media screen and (max-width: 1200px) {
    .list {
        &-left {
            flex-basis: 30% !important;
        }
    }
}

@media screen and (max-width: 1000px) {
    .list {
        &-right-button {
            width: 100% !important;
            div {
                margin: 0 1em;
            }
        }

        &-left {
            position: fixed;
            left: -100%;
            z-index: 1;
            height: 100%;
            width: 100vw;
            transition: 1s;
            }
    
            .filters-active {
                left: 0 !important;
            }
    }
}

@media screen and (max-width: 900px) {
    .list {
        
        .product-cart, .dog-cart {
            flex-basis: 100% !important;
        }
    }
}

@media screen and (max-width: 450px) {
    .list {
        
       .list-left {
        h2 {
            text-align: start;
        }
       } 
    }
}