.footer {
    background-color: $thirdColor;
    padding: 2em 5em;
    // box-shadow: 0px -5px 10px black;
    box-shadow: 0px -8px 6px -6px rgba(0, 0, 0, 0.5);
    z-index: 1;
    position: sticky;
    &-top {
        @include flex(space-between);
     &-navigation {
         ul {
            @include flex(center);
            flex-direction: column;
            li {
                padding: 0.25em;
                cursor: pointer;
                transition: 0.5s;
                font-size: 1.1em;
                &:hover {
                    color: black;
                }
            }
        }
     }
     &-middle {
        &-image {
            margin: 1em auto;
        } 
     }

     &-media {
        @include flex(center);
        flex-direction: column;
        svg {
            margin: 1em;
            scale: 2;
            cursor: pointer;
            &:hover {
                opacity: 0.6;
            }
        }
     }
    }
    &-bottom {
        margin-top: 2em;
        text-align: center;
    }   
}