.single-item {
    padding: 1em;
    &__container {
            align-items: start;
            .single-item-image__container {
                flex-basis: 50%;
                .single-item-image {
                    margin: 1em;
                    position: relative;
                    overflow: hidden;
                    height: 500px;
                    border-radius: 10px;
                    &:hover {
                        cursor: move;
                    }
                    img {
                        @include absolute(50%, 50%);
                        pointer-events: none;
                        // object-fit: contain;
                        
                    }
                }
                .single-item-images-list__container {
                    @include flex(space-between);
                    .single-item-images-list {
                        cursor: pointer;
                        flex-basis: 23%;
                        &:hover {
                            opacity: 0.8;
                        }
                        img {
                            border-radius: 5px;
                        }
                    }
                    .active {
                        opacity: 0.8 !important;
                        border-bottom: 2px solid $primaryColor !important;
                    }
                }
            }
            .single-item-info__container {
                flex-basis: 40%;
                padding: 1em;
                div {
                    padding: 1em 0;
                }

                .single-item-info-title {
                    text-align: center;
                }

                .single-item-info-others {
                    @include flex(space-between);
                    .single-item-info-availability {
                        padding: 0;
                        .quantity {
                            padding: 0;
                            @include flex(start);
                            flex-direction: column;
                            p {
                                font-size: 2em;
                                color: $successColor;
                            }
                        }
                        .danger {
                            padding: 0;
                            color: $dangerColor;
                        }
                    }
                    .single-item-info-price {
                        padding: 0;
                        border: 1px dotted $primaryColor;
                        height: 60px;
                        width: 60px;
                        display: flex;
                        border-radius: 50%;
                        background: white;
                        align-items: center;
                        justify-content: center;
                        h5 {
                            font-size: 1.5em;
                            color: $primaryColor;
                            text-shadow: 1px 1px 2px black;
                        }
                    }
                }
            }
        }
}

@media screen and (max-width: 1000px) {
    .single-item {
        &__container {
            flex-direction: column-reverse;
        }
        &-image {
            pointer-events: none;
        }
    }
}

@media screen and (max-width: 600px) {
    .single-item {
        &-image {
            height: 400px !important;
        }
    }
}

@media screen and (max-width: 500px) {
    .single-item {
        &-image {
            height: 300px !important;
        }
    }
}
