.admin {
    align-items: stretch;
    padding-bottom: 0 !important;
    &-left {
        width: 20%;
        padding: 2em;
        font-size: 1.5em;
        background: $secondaryColor;
        .admin-menu-list {
            list-style: none;
            &-element {
                padding: 0.5em;
                cursor: pointer;
                transition: 0.5s;
                &:hover {
                    color: $primaryColor;
                }
            }
            }
        }
    &-right {
        flex: 1;
        padding: 2em 4em;
        position: relative;
        &-button {
            width: 20%;
            @include absolute(10%, 80%);
        }
        h1 {
            text-align: center;
            padding: 1em;
            color: $primaryColor;
            text-shadow: 1px 1px 1px black;
        }
        .admin-list {
            list-style: none;
            h2 {
                @include absolute(50%, 50%);

            }
            .element {
                @include flex(space-between);
                font-size: 1.5em;
                width: 50%;
                margin: 0.25em auto;
                padding: 0.5em;
                border-radius: 20px;
                &-name {
                    flex: 1;
                }
                
                div[class^='element-'], div[class*=' element-']{
                    text-align: center;
                    margin: 0 0.5em;
                }
                &-delete {
                    color: red;
                    cursor: pointer;
                }
            }

            .even {
                background-color: #ffc124b8;
            }

            .odd {
                background-color: #daa520e0;
            }
        }
    }
}